import React, { useRef, useEffect, useState } from "react";

const DodgeTheCreeps = () => {
  const canvasRef = useRef(null);
  const requestRef = useRef();
  const [restartCounter, setRestartCounter] = useState(0);

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    canvas.width = 800;
    canvas.height = 600;

    // Variables iniciales del juego
    const player = {
      x: canvas.width / 2,
      y: canvas.height / 2,
      radius: 15,
      speed: 4,
    };
    // Variables para control de teclado y toques
    const keysPressed = {};
    let activeTouch = null;

    // Configuraci√≥n de enemigos ("creeps")
    const mobs = [];
    const mobSpeed = 2;
    const mobRadius = 10;
    const spawnInterval = 2000; // Intervalo de aparici√≥n en milisegundos
    let lastSpawnTime = 0;
    let gameOver = false;
    const startTime = Date.now();
    let score = 0;

    // Eventos de teclado
    const keyDownHandler = (e) => {
      keysPressed[e.key] = true;
    };
    const keyUpHandler = (e) => {
      keysPressed[e.key] = false;
    };
    document.addEventListener("keydown", keyDownHandler);
    document.addEventListener("keyup", keyUpHandler);

    // Eventos t√°ctiles para m√≥viles
    const touchStartHandler = (e) => {
      e.preventDefault();
      const touch = e.touches[0];
      const rect = canvas.getBoundingClientRect();
      activeTouch = {
        x: touch.clientX - rect.left,
        y: touch.clientY - rect.top,
      };
    };
    const touchMoveHandler = (e) => {
      e.preventDefault();
      const touch = e.touches[0];
      const rect = canvas.getBoundingClientRect();
      activeTouch = {
        x: touch.clientX - rect.left,
        y: touch.clientY - rect.top,
      };
    };
    const touchEndHandler = (e) => {
      e.preventDefault();
      activeTouch = null;
    };
    canvas.addEventListener("touchstart", touchStartHandler, { passive: false });
    canvas.addEventListener("touchmove", touchMoveHandler, { passive: false });
    canvas.addEventListener("touchend", touchEndHandler, { passive: false });
    canvas.addEventListener("touchcancel", touchEndHandler, { passive: false });

    // Utilidad para calcular la distancia entre dos puntos
    const distance = (ax, ay, bx, by) => Math.hypot(bx - ax, by - ay);

    // Funci√≥n para generar un enemigo en un borde aleatorio
    const spawnMob = () => {
      let x, y;
      const edge = Math.floor(Math.random() * 4); // 0: arriba, 1: derecha, 2: abajo, 3: izquierda
      if (edge === 0) {
        x = Math.random() * canvas.width;
        y = -mobRadius;
      } else if (edge === 1) {
        x = canvas.width + mobRadius;
        y = Math.random() * canvas.height;
      } else if (edge === 2) {
        x = Math.random() * canvas.width;
        y = canvas.height + mobRadius;
      } else {
        x = -mobRadius;
        y = Math.random() * canvas.height;
      }
      mobs.push({ x, y });
    };

    // Actualizaci√≥n del jugador: usa teclado o movimiento t√°ctil (si hay toque activo)
    const updatePlayer = () => {
      if (activeTouch) {
        const lerpFactor = 0.2; // Factor de interpolaci√≥n para un movimiento suave
        player.x += (activeTouch.x - player.x) * lerpFactor;
        player.y += (activeTouch.y - player.y) * lerpFactor;
      } else {
        if (keysPressed["ArrowUp"] || keysPressed["w"]) player.y -= player.speed;
        if (keysPressed["ArrowDown"] || keysPressed["s"]) player.y += player.speed;
        if (keysPressed["ArrowLeft"] || keysPressed["a"]) player.x -= player.speed;
        if (keysPressed["ArrowRight"] || keysPressed["d"]) player.x += player.speed;
      }

      // Evitar que el jugador salga del √°rea de juego
      if (player.x < player.radius) player.x = player.radius;
      if (player.x > canvas.width - player.radius) player.x = canvas.width - player.radius;
      if (player.y < player.radius) player.y = player.radius;
      if (player.y > canvas.height - player.radius) player.y = canvas.height - player.radius;
    };

    // Movimiento de los enemigos hacia el jugador
    const updateMobs = () => {
      mobs.forEach((mob) => {
        const dx = player.x - mob.x;
        const dy = player.y - mob.y;
        const angle = Math.atan2(dy, dx);
        mob.x += Math.cos(angle) * mobSpeed;
        mob.y += Math.sin(angle) * mobSpeed;
      });
    };

    // Detecci√≥n de colisiones entre el jugador y los enemigos
    const detectCollisions = () => {
      mobs.forEach((mob) => {
        if (distance(player.x, player.y, mob.x, mob.y) < player.radius + mobRadius)
          gameOver = true;
      });
    };

    // Dibuja al jugador (c√≠rculo verde)
    const drawPlayer = () => {
      ctx.beginPath();
      ctx.arc(player.x, player.y, player.radius, 0, Math.PI * 2);
      ctx.fillStyle = "#00FF00";
      ctx.fill();
      ctx.closePath();
    };

    // Dibuja a los enemigos (c√≠rculos rojos)
    const drawMobs = () => {
      mobs.forEach((mob) => {
        ctx.beginPath();
        ctx.arc(mob.x, mob.y, mobRadius, 0, Math.PI * 2);
        ctx.fillStyle = "#FF0000";
        ctx.fill();
        ctx.closePath();
      });
    };

    // Dibuja la puntuaci√≥n en pantalla
    const drawScore = () => {
      ctx.fillStyle = "#fff";
      ctx.font = "20px Arial";
      ctx.fillText("Score: " + score, 10, 30);
    };

    // Dibuja la pantalla de Game Over
    const drawGameOver = () => {
      ctx.fillStyle = "rgba(0, 0, 0, 0.7)";
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      ctx.fillStyle = "#fff";
      ctx.font = "40px Arial";
      ctx.fillText("Game Over", canvas.width / 2 - 100, canvas.height / 2);
    };

    // Bucle principal del juego
    const gameLoop = (timestamp) => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      if (gameOver) {
        drawGameOver();
        drawScore();
        return;
      }

      // Controla la aparici√≥n de nuevos enemigos
      if (timestamp - lastSpawnTime > spawnInterval) {
        spawnMob();
        lastSpawnTime = timestamp;
      }

      updatePlayer();
      updateMobs();
      detectCollisions();

      score = Math.floor((Date.now() - startTime) / 1000);

      drawPlayer();
      drawMobs();
      drawScore();

      requestRef.current = requestAnimationFrame(gameLoop);
    };

    requestRef.current = requestAnimationFrame(gameLoop);

    // Limpieza de eventos y animaci√≥n al desmontar o resetear el componente
    return () => {
      document.removeEventListener("keydown", keyDownHandler);
      document.removeEventListener("keyup", keyUpHandler);
      canvas.removeEventListener("touchstart", touchStartHandler);
      canvas.removeEventListener("touchmove", touchMoveHandler);
      canvas.removeEventListener("touchend", touchEndHandler);
      canvas.removeEventListener("touchcancel", touchEndHandler);
      cancelAnimationFrame(requestRef.current);
    };
  }, [restartCounter]);

  const handleReset = () => {
    // Al presionar el bot√≥n se incrementa el contador, lo que reinicia el useEffect
    setRestartCounter((prev) => prev + 1);
  };

  return (
    <div style={{ textAlign: "center" }}>
      <h1>Dodge The Creeps</h1>
      <button
        onClick={handleReset}
        style={{
          marginBottom: "10px",
          padding: "8px 16px",
          fontSize: "16px",
          cursor: "pointer",
        }}
      >
        Reset
      </button>
      <canvas
        ref={canvasRef}
        style={{
          border: "2px solid #fff",
          background: "#222",
          display: "block",
          margin: "0 auto",
        }}
      />
    </div>
  );
};

export default DodgeTheCreeps;
